import { useState, useEffect } from 'react';
import { Redirect, useParams } from 'react-router-dom';
import Container from '@material-ui/core/Container';
import Divider from '@material-ui/core/Divider';
import { DEFAULT_TITLE, courseCollection } from '@src/configs/env';
import { TitleBar, Loading, MediaStateProvider } from '@src/components';
import { useDocumentTitle } from '@src/hooks';
import { MessageHeading } from './MessageHeading';
import { MessageList } from './MessageList';

const { entities: courseEntities } = courseCollection;

export function DetailPage() {
  // @ts-ignore
  const { courseId } = useParams();

  const [fetchStatus, setFetchStatus] = useState(/** @type {LoadingState} */ ('none'));
  const [messageList, setMessageList] = useState(/** @type {MessageEntity[]} */ ([]));

  useDocumentTitle([courseEntities[courseId]?.title, DEFAULT_TITLE].filter(Boolean).join(' - '));

  useEffect(() => {
    const fetchCourseMessages = async () => {
      try {
        if (!courseEntities[courseId]) {
          setFetchStatus('failed');
          return;
        }

        setFetchStatus('loading');
        const { default: messageList } = await import(`@src/data/courses/${courseId}.json`);

        setMessageList(messageList);
        setFetchStatus('loaded');
      } catch (err) {
        setFetchStatus('failed');
      }
    };

    fetchCourseMessages();
  }, [courseId]);

  if (fetchStatus === 'failed') {
    return <Redirect to="/" />;
  }

  return (
    <MediaStateProvider>
      <TitleBar title={DEFAULT_TITLE} course={courseEntities[courseId]} back />
      <Container maxWidth="sm">
        {fetchStatus === 'loading' && <Loading />}
        {fetchStatus === 'loaded' && (
          <>
            {Boolean(courseEntities[courseId]) && (
              <MessageHeading course={courseEntities[courseId]} />
            )}
            <Divider />
            <MessageList messageList={messageList} />
          </>
        )}
      </Container>
    </MediaStateProvider>
  );
}
