import React, { createContext, useReducer, useContext } from 'react';
import { localStore } from '@src/utils';
import { PLAYBACK_RATE_STORED_KEY } from '@src/configs/pbr';

/**
 * @param {MediaState} state
 * @param {MediaAction} action
 */
const mediaReducer = (state, action) => {
  const { type, payload } = action;

  switch (type) {
    case 'updatePlaybackRate': {
      const playbackRate = payload['value'];
      localStore.set(PLAYBACK_RATE_STORED_KEY, playbackRate);
      return { ...state, playbackRate };
    }

    case 'switchPlayingMediaId': {
      return { ...state, playingMediaId: payload['id'] || null };
    }

    default:
      return state;
  }
};

const MediaStateContext = createContext(null);

/** @type {MediaState} */
const intialState = {
  playbackRate: 1,
  playingMediaId: null,
};

/**
 * @type {() => [MediaState, React.Dispatch<MediaAction>]}
 */
export const useMediaState = () => useContext(MediaStateContext);

export function MediaStateProvider({ children }) {
  return (
    <MediaStateContext.Provider value={useReducer(mediaReducer, intialState)}>
      {children}
    </MediaStateContext.Provider>
  );
}
