import { createEntityCollection } from '@src/lib';

/** `localStorage` key */
export const PLAYBACK_RATE_STORED_KEY = 'xow-pbr';

export const playbackRateCollection = createEntityCollection(
  [
    { text: '2.0x', value: 2 },
    { text: '1.5x', value: 1.5 },
    { text: '1.25x', value: 1.25 },
    { text: '正常倍速', value: 1 },
    { text: '0.75x', value: 0.75 },
  ],
  x => String(x.value),
);
